<template>
    <div v-if="patient.id">
        <div>
            <p-button
                label="Back"
                class="p-button-sm p-button-outlined"
                severity="info"
                raised
                style="margin-bottom: 20px;"
                @click="goToBarcodePage"
            />
        </div>
        <!-- First Section -->
        <div class="section">
            <div class="p-grid">
                <div class="p-col-12 block-container">
                    <div class="block-item">
                        <div style="margin-right: 15px;">
                            <img
                                v-if="patientPhoto"
                                :src="patientPhoto"
                                alt="Patient Photo"
                                style="height: 100px;"
                            />
                            <img
                                v-else-if="patient.sex === 'F'"
                                src="../../assets/women.png"
                                style="width: 100px; height: 100px;"
                                alt="Default Female"
                            />
                            <img
                                v-else
                                src="../../assets/men.png"
                                style="width: 100px; height: 100px;"
                                alt="Default Male"
                            />
                        </div>
                        <div>
                            <div style="margin-bottom: 10px;">
                                <div class="title">
                                    PATIENT
                                </div>
                                <span
                                    >{{ patient.firstName }}
                                    {{ patient.lastName }}</span
                                ><br />
                                <span class="p-id">{{ patient.id }}</span>
                            </div>

                            <span class="p-buttonset">
                                <p-button
                                    label="Barcode"
                                    class="p-button-sm"
                                    @click="showBarcodeV2(patient)"
                                />
                                <p-button
                                    class="p-button-sm"
                                    icon="pi pi-print"
                                    @click="printToZebraPrinter(patient)"
                                />
                            </span>
                        </div>
                    </div>
                    <div class="block-item" v-if="partner.id">
                        <div style="margin-right: 15px;">
                            <div style="margin-bottom: 10px;">
                                <div class="title">
                                    PARTNER
                                </div>
                                <span
                                    >{{ partner.firstName }}
                                    {{ partner.lastName }}</span
                                ><br />
                                <span class="p-id">{{ partner.id }}</span>
                            </div>
                            <span class="p-buttonset">
                                <p-button
                                    label="Barcode"
                                    class="p-button-sm"
                                    @click="showBarcodeV2(partner)"
                                />
                                <p-button
                                    class="p-button-sm"
                                    icon="pi pi-print"
                                    @click="printToZebraPrinter(partner)"
                                />
                            </span>
                        </div>
                        <div>
                            <!-- Partner Image -->
                            <img
                                v-if="partnerPhoto"
                                :src="partnerPhoto"
                                alt="Partner Photo"
                                style="height: 100px;"
                            />
                            <img
                                v-else-if="partner.sex === 'F'"
                                src="../../assets/women.png"
                                style="width: 100px; height: 100px;"
                                alt="Default Female"
                            />
                            <img
                                v-else
                                src="../../assets/men.png"
                                style="width: 100px; height: 100px;"
                                alt="Default Male"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- Second Section -->
        <div class="section" style="margin-top: 20px;">
            <div class="container" style="background: ;">
                <!-- Left Box -->
                <div
                    class="left-box"
                    style="display: inline-block; width: 50%; border-right: dashed 1px grey; min-height: 400px;"
                >
                    <div v-for="item in patientLabTests" class="inline-box">
                        <div>
                            <div
                                style="margin-bottom: 10px; font-weight: bold; display: flex; justify-content: center; align-content: center;"
                            >
                                <span> {{ item.name }} </span><br />
                            </div>
                            <div
                                style="display: flex; justify-content: center; align-content: center;"
                            >
                                <img
                                    :src="getIconUrl(item)"
                                    alt="Image"
                                    style="width: 50px; height: 50px"
                                />
                            </div>
                            <div>
                                <span
                                    class="p-buttonset"
                                    style="display: flex;"
                                >
                                    <p-button
                                        label="Barcode"
                                        class="p-button-sm"
                                        @click="printLabelJSPDF(patient, item)"
                                    />
                                    <p-button
                                        class="p-button-sm"
                                        icon="pi pi-print"
                                        @click="printLabelJSPDF(patient, item)"
                                    />
                                </span>
                            </div>
                        </div>
                    </div>
                </div>

                <!-- Right Box -->
                <div
                    class="right-box"
                    style="display: inline-block;  width: 50%; min-height: 400px;"
                >
                    <div v-for="item in partnerLabTests" class="inline-box">
                        <div>
                            <div
                                style="margin-bottom: 10px; font-weight: bold; display: flex; justify-content: center; align-content: center;"
                            >
                                <span> {{ item.name }} </span><br />
                            </div>
                            <div
                                style="display: flex; justify-content: center; align-content: center;"
                            >
                                <img
                                    :src="getIconUrl(item)"
                                    alt="Image"
                                    style="width: 50px; height: 50px"
                                />
                            </div>
                            <div>
                                <span
                                    class="p-buttonset"
                                    style="display: flex;"
                                >
                                    <p-button
                                        label="Barcode"
                                        class="p-button-sm"
                                        @click="printLabelJSPDF(partner, item)"
                                    />
                                    <p-button
                                        class="p-button-sm"
                                        icon="pi pi-print"
                                        @click="printLabelJSPDF(patient, item)"
                                    />
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div v-else>
        <h2>No patient selected</h2>
    </div>
</template>
<script>
import { $axios } from "@/service/app-http";
import { onMounted, ref, watch } from "vue";
import { useStore } from "vuex";
import { serialize } from "php-serialize";
import { ZEBRA_PRING_URL, NL_FORM_BASE } from "@/constants";
import { ORIGIN } from "./../../config";
import { useRouter } from "vue-router";

import JsBarcode from "jsbarcode";
import jsPDF from "jspdf";
import { Canvg } from "canvg";

export default {
    setup() {
        const printToZebraPrinter = (patient) => {
            let base = `${ZEBRA_PRING_URL}/patient_zpl_print.php`;

            let url =
                `${base}` +
                `?first_name=${patient.firstName}` +
                `&last_name=${patient.lastName}` +
                `&chart=${patient.chart}` +
                `&dob=${patient.year}-${patient.month}-${patient.day}` +
                `&gender=${patient.sex}`;

            window.open(url);
        };
        const store = useStore();
        const logsData = ref([]);

        const patientLabTests = ref([]);
        const partnerLabTests = ref([]);

        const loading = ref(false);

        const patient = ref({});
        const partner = ref({});

        const patientPhoto = ref(null);
        const partnerPhoto = ref(null);

        const router = useRouter();

        const loadPhotos = async () => {
            try {
                if (patient.value.id) {
                    const [patientRes, partnerRes] = await Promise.all([
                        $axios.get(`photo/by-patient/${patient.value.id}`),
                        partner.value.id
                            ? $axios.get(`photo/by-patient/${partner.value.id}`)
                            : Promise.resolve({
                                  data: { photo: null, content_type: null }
                              })
                    ]);

                    patientPhoto.value = patientRes.data?.photo
                        ? `data:${patientRes.data.content_type};base64,${patientRes.data.photo}`
                        : null;

                    partnerPhoto.value = partnerRes.data?.photo
                        ? `data:${partnerRes.data.content_type};base64,${partnerRes.data.photo}`
                        : null;

                    console.log("Loaded photos:", {
                        patientPhoto: patientPhoto.value,
                        partnerPhoto: partnerPhoto.value
                    });
                }
            } catch (error) {
                console.error("Error loading photos:", error);
                patientPhoto.value = null;
                partnerPhoto.value = null;
            }
        };

        const showBarcodeV2 = (patient) => {
            const payload = {
                bar_id: patient.id
            };

            let url = `${NL_FORM_BASE}/jrsPHPclientV2/jrs/generate_reportn1.php?report_name=TESTS_1&report_type=pdf&report_params=${encodeURIComponent(
                serialize(payload)
            )}`;
            window.open(url);
        };

        const createBarcodeJasper = async (patient, item) => {
            try {
                const { data } = await $axios.post(`lab-barcode/create`, {
                    patient_id: patient.id,
                    container_id: item.labTestId,
                    container_name: item.name
                });

                const payload = {
                    bar_id: data.model.barcode
                };
                let url = `${NL_FORM_BASE}/jrsPHPclientV2/jrs/generate_reportn1.php?report_name=TESTS_1_1&report_type=pdf&report_params=${encodeURIComponent(
                    serialize(payload)
                )}`;
                window.open(url);
            } catch (err) {}
        };

        const createBarcodeZebra = async (patient, item) => {
            try {
                const { data } = await $axios.post(`lab-barcode/create`, {
                    patient_id: patient.id,
                    container_id: item.labTestId,
                    container_name: item.name
                });

                let base = `${ZEBRA_PRING_URL}/labContainer_zpl_print.php`;

                let url =
                    `${base}` +
                    `?barcode=${data.model.barcode}&label=${item.name}&chartid=${patient.id}`;

                window.open(url);
            } catch (err) {}
        };

        const printCitizenLabel = async (patient, item) => {
            try {
                const { data } = await $axios.post(`lab-barcode/create`, {
                    patient_id: patient.id,
                    container_id: item.labTestId,
                    container_name: item.name
                });

                const barcodelbl = data.model.barcode;

                const htmlc = `
<html>
<head>
  <style>
    @page {
    size: 10mm 38mm;
    margin: 0;
  }

  html, body {
    width: 10mm;
    height: 38mm;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }

  .label {
    width: 10mm;        /* original height */
    height: 38mm;       /* original width */
    transform: rotate(-90deg);
    transform-origin: top left;
    position: absolute;
    top: 0;
    left: -38mm; /* ‚Üê shift left so rotated label appears in view */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-family: Arial, sans-serif;
  }

  .top-text {
    height: 5mm;
    padding-left: 1mm;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .text1 {
    font-size: 7pt;
    line-height: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .text2 {
    font-size: 5pt;
    line-height: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .barcode {
    height: 5mm;
    padding: 0 1mm;
  }

  svg {
    width: 100%;
    height: 100%;
    display: block;
  }
  </style>
</head>
<body>
  <div class="label">
    <div class="top-text">
        <div class="text1">${item.name} ${patient.firstName} ${patient.lastName}</div>
        <div class="text2">${patient.id}</div>
    </div>
    <div class="barcode">
      <svg id="barcode"></svg>
    </div>
  </div>
</body>
</html>
`;

                const iframec = document.createElement("iframe");
                iframec.style.display = "none";
                document.body.appendChild(iframec);

                const doc = iframec.contentWindow.document;
                doc.open();
                doc.write(htmlc);
                doc.close();

                iframec.onload = () => {
                    const script = doc.createElement("script");
                    script.src =
                        "https://cdn.jsdelivr.net/npm/jsbarcode@3.11.5/dist/JsBarcode.all.min.js";
                    script.onload = () => {
                        iframec.contentWindow.JsBarcode(
                            "#barcode",
                            barcodelbl,
                            {
                                format: "CODE128",
                                width: 2.4, // May need tuning for this printer
                                height: 38, // ~5mm
                                lineColor: "#000",
                                displayValue: false,
                                margin: 0
                            }
                        );

                        setTimeout(() => iframec.contentWindow.print(), 300);
                    };
                    doc.body.appendChild(script);
                };
            } catch (err) {
                console.error("Print label error:", err);
            }
        };

        const printLabelJSPDF = async (patient, item) => {
            try {
                const { data } = await $axios.post(`lab-barcode/create`, {
                    patient_id: patient.id,
                    container_id: item.labTestId,
                    container_name: item.name
                });

                const barcodelbl = data.model.barcode;

                const fullName = `${item.name} ${patient.firstName} ${patient.lastName}`;

                const svg = document.createElement("svg");
                JsBarcode(svg, barcodelbl, {
                    format: "CODE128",
                    width: 4,
                    height: 40,
                    displayValue: false,
                    margin: 0
                });

                // Remove duplicate xmlns
                svg.removeAttribute("xmlns");

                // Render SVG to Canvas
                const canvas = document.createElement("canvas");
                canvas.width = 200;
                canvas.height = 40;
                const ctx = canvas.getContext("2d");

                const svgString = new XMLSerializer().serializeToString(svg);
                const canvgInstance = await Canvg.from(ctx, svgString);
                await canvgInstance.render();

                const pngDataUrl = canvas.toDataURL("image/png");

                // Create PDF
                const doc = new jsPDF({
                    orientation: "landscape",
                    unit: "mm",
                    format: [38, 10] // Label size
                });

                doc.setFontSize(6);
                doc.text(fullName, 1, 2);
                doc.setFontSize(5);
                doc.text(String(patient.id), 1, 4);
                doc.addImage(pngDataUrl, "PNG", 1, 6, 36, 3.5);

                // Generate PDF blob
                const pdfBlob = doc.output("blob");
                const blobUrl = URL.createObjectURL(pdfBlob);

                // Create hidden iframe
                const iframe = document.createElement("iframe");
                iframe.style.position = "fixed";
                iframe.style.right = "0";
                iframe.style.bottom = "0";
                iframe.style.width = "0";
                iframe.style.height = "0";
                iframe.style.border = "0";
                iframe.src = blobUrl;

                document.body.appendChild(iframe);

                iframe.onload = () => {
                    iframe.contentWindow.focus();
                    iframe.contentWindow.print();
                };
            } catch (err) {
                console.error("Print label error:", err);
            }
        };

        const getIconUrl = (data) => {
            if (data.icon) {
                return `${ORIGIN}/${data.icon}`;
            } else {
                return "https://via.placeholder.com/300";
            }
        };

        const getPatientLabtests = async (gender) => {
            if (gender == "M" || gender == "m") gender = "male";
            if (gender == "F" || gender == "f") gender = "female";

            const { data } = await $axios.get(`lab-tests/list/${gender}`, {
                params: {}
            });

            patientLabTests.value = data;
        };

        const getPartnerLabtests = async (gender) => {
            if (gender == "M" || gender == "m") gender = "male";
            if (gender == "F" || gender == "f") gender = "female";

            const { data } = await $axios.get(`lab-tests/list/${gender}`, {
                params: {}
            });

            partnerLabTests.value = data;
        };

        // const onFilterHandler = () => {
        //     getLogs();
        // };

        onMounted(() => {
            console.log(store.state.artCycle.patient);
            // getLogs();
            if (store.state.artCycle.patient) {
                patient.value = store.state.artCycle.patient;

                getPatientLabtests(patient.value.sex);
            }
            if (store.state.artCycle.partner) {
                partner.value = store.state.artCycle.partner;

                getPartnerLabtests(partner.value.sex);
            }

            loadPhotos();
        });

        return {
            logsData,
            loading,
            patient,
            partner,
            patientPhoto,
            partnerPhoto,
            store,
            patientLabTests,
            partnerLabTests,
            showBarcodeV2,
            getIconUrl,
            printToZebraPrinter,
            createBarcodeJasper,
            createBarcodeZebra,
            printCitizenLabel,
            printLabelJSPDF,
            goToBarcodePage: () => {
                router.push({ path: "/barcodes" });
            }
        };
    }
};
</script>

<style scoped>
.p-card .p-card-content {
    padding: 0;
}

.block-item {
    display: flex;
    border: 1px solid #8cb3d9;
    padding: 10px;
    background-color: white;
}
.block-container {
    display: flex;
    justify-content: space-between;
}
.title {
    margin-bottom: 10px;
    color: brown;
    font-weight: bolder;
}
.p-id {
    color: darkblue;
    font-weight: bolder;
}

/* Inline boxes */
.inline-box {
    margin: 4px 4px; /* Add margin for spacing between boxes */
    border: 1px solid #8cb3d9;
    display: inline-block;
}

/* Align left box content */
.left-box {
    text-align: left;
    vertical-align: top;
}

/* Align right box content */
.right-box {
    text-align: right;
    vertical-align: top;
}

.inline-box img {
    width: 100px;
    border-radius: 50%; /* Make the image a circle */
}
</style>
